import React from "react";

import "./NavigationItems.scss";

import NavigationItem from "./NavigationItem/NavigationItem";


const navigationItems = ({ clicked }) => {
  return (
    <ul className='navigationItems'>
      <div>
        <NavigationItem clicked={clicked} link='/test-playground/#contactus'>contact us</NavigationItem>
        <NavigationItem clicked={clicked} link='/test-playground/#careers'>careers</NavigationItem>

        <NavigationItem clicked={clicked} link='/test-playground/#wedo'>we do</NavigationItem>
        <NavigationItem clicked={clicked} link='/test-playground/#weare'>we are</NavigationItem>
      </div>
    </ul>
  );
};

export default navigationItems